import { request } from 'utils';

const prefix = '/api/categories';

export const getDormitoryRooms = ({
  dormId,
  floorId,
  roomNo,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/Dormitory/GetDormitoryRoomCollection`,
    method: 'GET',
    params: { dormId, floorId, roomNo, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createDormitoryRoom = ({ dormId, floorId, roomNo, size, device, feePerPerson } = {}) =>
  request({
    prefix,
    url: `/Dormitory/CreateDormitoryRoom`,
    method: 'POST',
    params: {},
    data: { dormId, floorId, roomNo, size, device, feePerPerson },
  });

export const getDormitoryRoomById = ({ roomId, dormId, floorId } = {}) =>
  request({
    prefix,
    url: `/Dormitory/GetDormitoryRoomById`,
    method: 'GET',
    params: { roomId, dormId, floorId },
    data: {},
  });

export const updateDormitoryRoom = ({
  dormId,
  floorId,
  roomId,
  roomNo,
  size,
  device,
  feePerPerson,
} = {}) =>
  request({
    prefix,
    url: `/Dormitory/UpdateDormitoryRoom`,
    method: 'PUT',
    params: {},
    data: { dormId, floorId, roomId, roomNo, size, device, feePerPerson },
  });

export const deleteDormitoryRoom = ({ dormId, floorId, roomId } = {}) =>
  request({
    prefix,
    url: `/Dormitory/DeleteDormitoryRoom`,
    method: 'DELETE',
    params: {},
    data: { dormId, floorId, roomId },
  });



