import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { InputMoney, SelectAjax } from 'components/common';
import { Row, Card, Form, Input, InputNumber } from 'antd';
import {
  getDormitoryRoomById,
  updateDormitoryRoom,
  createDormitoryRoom,
} from 'services/dormitoryRoom';
import { getDormitorys, getDormitoryFloors } from 'services/dormitory';
import { getQueryParams } from 'utils/urlUtil';
import { requiredRule } from 'utils/validateRules';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    dormId: '',
    floorId: '',
    roomNo: '',
    size: 100,
    device: '',
    feePerPerson: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormDormitoryRoomPage' })
class FormDormitoryRoomPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const { dormitoryId, floorId } = getQueryParams();
    const res = await getDormitoryRoomById({ roomId: id, dormId: dormitoryId, floorId });
    const initialValue = handleInitialValue(res.data);
    initialValue.dormId = dormitoryId;
    initialValue.floorId = floorId;
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId, initialValue } = this.state;
    try {
      if (!dataId) {
        await createDormitoryRoom(values);
      } else {
        await updateDormitoryRoom({ roomId: dataId, floorId: initialValue.floorId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    const dormId = getFieldValue('dormId');
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} phòng KTX`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item hasFeedback label="Tòa KTX">
              {getFieldDecorator('dormId', {
                initialValue: initialValue.dormId,
                rules: [requiredRule],
              })(<SelectAjax disabled={!!dataId} service={getDormitorys} />)}
            </Form.Item>
            <Form.Item label="Tầng" hasFeedback>
              {!dataId ? (
                getFieldDecorator('floorId', {
                  initialValue: initialValue.floorId,
                  rules: [requiredRule],
                })(
                  <SelectAjax
                    disabled={!!dataId}
                    setFieldsValue={this.props.form.setFieldsValue}
                    params={{ dormId }}
                    service={getDormitoryFloors}
                    allowGetData={!!dormId}
                  />
                )
              ) : (
                <Input disabled value={initialValue.floorId} />
              )}
            </Form.Item>
            <Form.Item label="Số phòng" hasFeedback>
              {getFieldDecorator('roomNo', {
                initialValue: initialValue.roomNo,
                rules: [requiredRule],
              })(<Input disabled={!!dataId} />)}
            </Form.Item>
            <Form.Item label="Sức chứa" hasFeedback>
              {getFieldDecorator('size', {
                initialValue: initialValue.size,
                rules: [requiredRule],
              })(<InputNumber min={0} max={100} />)}
            </Form.Item>
            <Form.Item label="Thiết bị">
              {getFieldDecorator('device', {
                initialValue: initialValue.device,
              })(<Input.TextArea autosize />)}
            </Form.Item>
            <Form.Item label="Số tiền" hasFeedback>
              {getFieldDecorator('feePerPerson', {
                initialValue: initialValue.feePerPerson,
                rules: [requiredRule],
              })(<InputMoney min={0} style={{ width: 200 }} />)}
            </Form.Item>
            <Form.Item label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormDormitoryRoomPage {...props} />
  </Page>
));



